	export default {
        onLoad() {
            this.list = []
            this.getList()
        },
        onReachBottom(){
            this.page = this.page + 1
            this.getList()
        },
		data() {
			return {
				list: [],
                page: 1,
                soureidList:[],
			}
		},
		methods: {
			getList(){
                uni.showLoading({
                    title: '正在加载中...',
                    mask: false
                })
                uni.request({
                    url:'https://www.apiopen.top/satinGodApi',
                    data: {
                        type: '5',
                        page: this.page
                    },
                    success: (res) => {
                        uni.hideLoading()
                        if(res.data.code===200){
                            this.list.push(...res.data.data)
                        }
                        
                    }
                })
            },
            collect(item){
                // console.log(item.soureid)
                if(this.soureidList.indexOf(item.soureid)<0){
                    this.soureidList.push(item.soureid)
                    uni.showToast({
                        title:'收藏成功！'
                    })
                }else{
                    this.soureidList.splice(this.soureidList.indexOf(item.soureid),1)
                    uni.showToast({
                        title:'取消收藏！'
                    })
                }
            },
            share(item){
                uni.showModal({
                    content: '分享功能正在开发中...',
                    showCancel: false
                });
            }
		}
	}
